import React from "react";
import styled from "styled-components";
import ImgAva from '../assets/img/Rectangle45.jpg'
import { fontStyles, TitleText, Gradient, GradientFon, MediaQuaeries } from "../styled/component";

const AboutMyBlock = styled.div`
    position: relative;
    display: flex;
    justify-content: revert;
    padding: 10px;
    background: white;
    &::before{
        width: 100%;
        height: 100%;
        content: "";
        position: absolute;
        top: 0;
        border-radius: 10px;
        background: linear-gradient(to right top, #7F0035, #0A007F);
        z-index: -1;
    }
    @media ${MediaQuaeries.media.medium960} {
        flex-direction: column;

    }
    @media  ${MediaQuaeries.media.smal450} {
        flex-direction: column;
        justify-content: center;
    }
    img{
        max-width: 620px;
        height: fit-content;
        margin: 0 10px;
        @media  ${MediaQuaeries.media.medium960} {
            margin:auto;
            width: 400px;
    }
        @media  ${MediaQuaeries.media.smal450} {
            margin:auto;
            width: 300px;
    }
    }
`
const TextAboutMy = styled.div`
    padding: 0 10px;
    overflow: hidden;
  ${fontStyles}
  @media  ${MediaQuaeries.media.smal450} {
        padding: 0 ;}
`
const Title = styled(TitleText)`
    padding: 0 0 0 90px;
    @media  ${MediaQuaeries.media.smal450} {
        font-size: 22px;
        text-align: start;
        padding: 0 15px;
      
      
    }
`


export const AboutMy = () => {
    return (
        <Gradient>
            <GradientFon>
            <Title > обо мне </Title>
            <AboutMyBlock>
                <img src={ImgAva} alt="" />
                <TextAboutMy>
                    <span>
                        Dj Alyssa. Настоящее имя - Тухикян Виктория.
                        Интерес к диджеингу возник ещё в студенческие годы,
                        когда в моем городе открыли школу диджеев в 2008 году.
                        После окончания курсов,
                        Я сразу же стала резидентом в престижном клубе города.
                        Озвучивала различные мероприятия, играла на частных вечеринках.
                        <br />
                        Не так давно у меня родилась идея создать свое диджей шоу,
                        где Я выступаю в светодиодном костюме. Также у меня есть шоу балет,
                        который вносит яркий акцент в мои выступления. Номера мы ставим совместно,
                        исполняя некоторые партии хореографии синхронно .
                        Так что, я не только играющий,но и танцующий диджей.
                        <p />
                        В своих сэтах иногда совмещаю игру на варгане под электронную музыку.
                        Этническое звучание варгана создаёт
                        ощущение транса и воссоединения с музыкой.
                        <p />
                        Я работала во множестве клубов, которые не пересчитать на пальцах.
                        Новые вечеринки я всегда освещаю в своих социальных сетях и телеграм канале.
                        <p />
                        Так что следите за новостям!
                    </span>

                </TextAboutMy>
            </AboutMyBlock>
            </GradientFon>
        </Gradient>
    )
}